:root {
  /* 62.5% od defaultnih 16px browsera je 10px... tako je laÅ¡e koristit rem jedinice */
  font-size: 62.5%;

  --fontSize: 1.4rem;
  --fontSizeLarge: 1.6rem;
  /* boje */
  --bgplava: rgb(150, 220, 250);
  --c-bgplava: 150 220 250;
  --svplava: rgb(0, 158, 225); /* svijetlo plava */
  --c-svplava: 0 158 225; /* svijetlo plava */
  --plava: rgb(0, 114, 188);
  --c-plava: 0 114 188; /* more plava */
  --tplava: rgb(16, 44, 105); /* tamno plava */
  --c-tplava: 16 44 105; /* tamno plava */
  --zuta: rgb(255, 202, 0); /* aurea zuta */
  --c-zuta: 255 202 0; /* aurea zuta */
  --zelena: rgb(23, 153, 53); /* zelena */
  --c-zelena: 23 153 53; /* zelena */
  --svzelena: rgb(33, 221, 77); /* svijetlo zelena */
  --c-svzelena: 33 221 77;
  --bgzelena: rgb(182, 242, 196);
  --c-bgzelena: 182 242 196;
  --siva: rgb(111, 113, 115); /* siva */
  --c-siva: 111 113 115; /* siva */
  --c-siva: 111 113 115; /* siva */
  --svsiva: rgb(198, 201, 204); /* svijetlo siva */
  --bgsiva: rgb(235, 239, 242); /* svijetlo siva */
  --crvena: rgb(249, 36, 96); /* alert crvena */

  --bodyColor: var(--tplava);
  --headingColor: var(--plava);
  --linkColor: var(--plava);
  --linkColorHover: var(--plava);

  /* font */
  /* --headingFont: "Minion Pro", "Times New Roman", Times, Georgia, "DejaVu Serif", serif; */
  /* --headingFont: 'Oswald', sans-serif; */
  --headingFont: 'Inter', sans-serif;
  --bodyFont: 'Inter', sans-serif;
  --linkFont: 'Inter', sans-serif;

  /* breakpoints */
  --mali: 'screen' 600px;
  --malipx: 600px;
  --srednji: 'screen' (min-width 960px) (min-height 800px);
  --veliki: 1160px;

  /* razno */
  --sirina: 960px;
  --maxSirina: 1160px;
  --marginSadrzaj: 20px;

  --izletBriefColor: var(--plava);
  --c-izletBriefColor: var(--c-plava);
}

.bojazelena {
  --izletBriefColor: var(--zelena);
  --c-izletBriefColor: var(--c-zelena);
}

.bojazuta {
  --izletBriefColor: var(--zuta);
  --c-izletBriefColor: var(--c-zuta);
}

/* media queries */

@custom-media --mobile only screen and (width >= 680px);
@custom-media --tablet only screen and (width >= 960px);
@custom-media --large only screen and (width >= 1200px);

/* custom selectors  */

@custom-selector :--heading h1, h2, h3, h4, h5, h6;
@custom-selector :--link a, a.link, a.visited, a.active;

