html,
body {
  margin: 20px;
  color: #000;
  background-color: #fff;
}

html body * {
  background-color: #fff;
  color: #000;
}

header,
video,
.triper,
.businessmenu,
.menusocialnetwork,
.triper,
.break,
.promolink,
.selectadditionaldate,
.printlink,
.izlet-video-wrapper,
.izlet-promolink,
.fast-menu-bottom,
.swiper-pagination-progressbar,
.swiper-pagination-progressbar-fill {
  display: none;
}

.content {
  margin: 0 auto;
}

.footer {
  margin: 5rem auto;
  padding-top: 5rem;
  border-top: 1px dotted #999;
}
.menukontakti {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.menukontakti > * {
  margin: 0 10px;
}
.copy {
  text-align: center;
}
