body,
button,
input,
select,
optgroup,
textarea {
  color: var(--bodyColor);
  font-family: var(--bodyFont);
  font-weight: lighter;
  font-weight: 337;
  font-variation-settings: 'wght' 337;
  /* font-size: 16px; */
  line-height: 1.4;
  font-size: var(--fontSize);
  @media (--large) {
    font-size: var(--fontSizeLarge);
  }
}

.fancy {
  font-family: 'Kaushan Script', cursive;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  font-family: var(--headingFont);
  color: var(--headingColor);
  font-weight: normal;
  font-weight: 337;
  font-variation-settings: 'wght' 337;
  letter-spacing: -0.1rem;
  line-height: 1.15;
}

h1 {
  font-size: 3rem;
  font-size: calc(3rem + 1.66vw);
  /* font-size: calc(4.5rem + 5.294vw); */
  line-height: 1.1;
  /* @media (--mobile) {
    font-size: 4rem;
    font-size: calc(3rem + 1.66vw);
  } */
  @media (--large) {
    font-size: 5rem;
  }
  margin: 1rem 0;
}

h2 {
  font-size: calc(var(--fontSize) * 2);
}

h3 {
  font-size: calc(var(--fontSize) * 1.6);
}

h4 {
  font-size: calc(var(--fontSize) * 1.4);
}

h5,
h6 {
  font-size: var(--fontSize);
}

p {
  /* font-family: 'Work+Sans'; */
  color: var(--bodyColor);
  margin-bottom: 1.5rem;
  font-weight: normal;
  font-weight: 337;
  font-variation-settings: 'wght' 337;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

pre {
  background: #eee;
  font-family: 'Courier 10 Pitch', Courier, monospace;
  font-size: 15px;
  font-size: 1.5rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code,
kbd,
tt,
var {
  font-family: Monaco, Consolas, 'Andale Mono', 'DejaVu Sans Mono', monospace;
  font-size: 15px;
  font-size: 1.5rem;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/* Links - boja i font */

:--link,
a:hover {
  color: var(--linkColor);
  text-decoration: none;
  font-family: var(--linkFont);
  font-weight: normal;
  font-weight: 337;
  font-variation-settings: 'wght' 337;
  transition: all 0.1s linear;
}

a:hover {
  color: var(--linkColorHover);
}

