/* // PRIKAZ IZLETA U PROMO TRACI  */
.promoizleti {
  margin: 5rem auto;
  display: grid;
  justify-content: center;
  align-items: stretch;
  align-content: start;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;

  /* grid-template-rows: repeat(3, auto); */
}

.link-izlet {
  display: flex;
  flex-direction: column;
}

.promoizlet {
  border: 1px dotted #999;
  flex: 1 1 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  position: relative;
  & header {
    flex: 1 1 auto;
    order: 1;

    display: block;
  }
  & .bookinglist {
    order: 3;
  }
  & .promoizletblok {
    order: 2;
    background: none !important;
  }
  & h3 {
    margin: 0;
  }
  & p {
    line-height: 1.1em;
    margin: 1em 0 0;
  }
}

.izletbrief {
  text-align: left;
  padding: 10px 0px;
  & p {
    color: #000;
  }
  & .cijenaod {
  }

  & .cijenafrom:before {
    content: 'FROM';
  }
  & .cijenaodhr:before {
    content: 'OD';
  }
  & .cijenaeur {
    &:after {
      content: ' EUR';
    }
  }
  & .trajanje {
    &:before {
      content: '\2248';
      content: 'trajanje: ';
    }
    &:after {
      content: ' h';
    }
  }

  & .cijena {
    &:before {
      content: 'cijena: ';
    }
    &:after {
      content: ' HRK';
    }
  }

  & .osobe {
    &:after {
      content: ' osoba/persons';
    }
  }
}

a.booknow {
  display: none;
}

/* // prikaz bookinjgdatuma na prikazu svih izleta */

.bookinglist {
  /* background-color: #eee; */
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: flex-start;
  & li {
    font-size: 0.7em;
    text-align: center;
    padding: 0 10px 0 0;
    margin: 0 10px 0 0;
    list-style-type: none;
    text-align: center;
    border-right: 1px dotted #999;
    & a {
      display: block;
      color: #000;
      letter-spacing: 0.09em;
      text-transform: uppercase;
      font-size: 1.5rem;
      & span {
        font-size: 1rem;
        display: block;
        margin-bottom: 0.8em;
      }
    }
  }
}

/* NO DATE nema dostupnih datuma za booking */

.promoizlet-nodates {
  padding: 0 10px 15px 2px;
  border-top: 1px dotted var(--svsiva);
  & p {
    font-size: 0.9em;
    color: #666;
  }
}

/* // PRIKAZ MAXI PROMOIZLETA NA NASLOVNICI  */
.maxipromoizleti-wrapper {
  width: 100%;
  & > h2,
  & > h3 {
    max-width: var(--maxSirina);
    margin: 5rem auto 0 auto;
    text-align: center;
    color: #666;
  }
}
.maxipromoizleti {
  margin: 5rem auto 5rem auto;
  /* text-align: center; */
  display: grid;
  justify-content: center;
  align-content: start;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
}

.maxipromoizleti {
  & h3 {
    font-size: 2.5rem;
  }
  & .promoizletblok {
    position: relative;
    overflow: hidden;
  }
  & .note {
    padding: 12px 80px 80px;
    background-color: #999;
    color: #fff;
    transform: rotate(-35deg);
    position: absolute;
    bottom: -50px;
    right: -85px;
    font-size: 1.8rem;
    font-weight: bold;
  }
}

.swiper-wrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

img {
  max-width: 100%;
  height: auto;
}
